$almost-black: #0f1112;
$brownish-grey: #666666;
$desert: #ccac5b;
$dark-grey: #22272b;
$cool-grey: #a7a9ac;

$open-sans: 'Open Sans', sans-serif;

@mixin header_big {
  font-family: $open-sans; }

@mixin header_sml {
  font-size: 24px; }

@mixin basic {
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 1px; }

@mixin basic_exp {
  @include basic;
  letter-spacing: 9px; }

@mixin basic_exp_semibold {
  @include basic;
  font-weight: 600;
  letter-spacing: 9px; }
